// src/libraries/UploadedLayoutManager.js

// 👇 Safe random ID generator for frontend
const generateId = () => {
  return Math.random().toString(16).slice(2) + Date.now().toString(16);
};

let uploadedCells = [];

const UploadedLayoutManager = {
  load(jsonArray) {
    uploadedCells = jsonArray.map(cell => ({
      name: cell.name,
      elements: (cell.elements || []).map(el => ({
        ...el,
        id: generateId()
      })),
      instances: (cell.instances || []).map(inst => ({
        ...inst,
        id: generateId(),
        scale: inst.scale || 1,
        rotation: inst.rotation || 0
      }))
    }));
  },

  getCells() {
    return uploadedCells;
  },

  getCellByName(name) {
    return uploadedCells.find(cell => cell.name === name);
  },

  reset() {
    uploadedCells = [];
  }
};

export default UploadedLayoutManager;