import React, { useState } from 'react';
import layerLibrary from '../canvas/LayerLibrary';
import AddIcon from '@mui/icons-material/Add';
import DownloadIcon from '@mui/icons-material/Download';
import UploadIcon from '@mui/icons-material/Upload';
import SortByAlphaIcon from '@mui/icons-material/SortByAlpha';
import FormatListNumberedIcon from '@mui/icons-material/FormatListNumbered';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import LockIcon from '@mui/icons-material/Lock';
import LockOpenIcon from '@mui/icons-material/LockOpen';
import FlipIcon from '@mui/icons-material/Flip';
import GroupWorkIcon from '@mui/icons-material/GroupWork';
import EditIcon from '@mui/icons-material/Edit';
import SaveIcon from '@mui/icons-material/Save';

const LayerTab = () => {
  const [expandedGroups, setExpandedGroups] = useState({});
  const [_, forceUpdate] = useState(0);
  const [search, setSearch] = useState('');
  const [grouped, setGrouped] = useState(false);
  const [dragIndex, setDragIndex] = useState(null);


  const layers = layerLibrary.getAllLayers();
  const groupedLayers = layerLibrary.getGroupedLayers();

  const toggleGroup = (groupName) => {
    setExpandedGroups((prev) => ({
      ...prev,
      [groupName]: !prev[groupName],
    }));
  };

  const toggleVisibility = (layer) => {
    layerLibrary.toggleVisibility(layer);
    forceUpdate((p) => p + 1);
  };

  const toggleLock = (layer) => {
    layerLibrary.toggleLock(layer);
    forceUpdate((p) => p + 1);
  };

  // Toggle edit mode for a layer
  const toggleEdit = (layer) => {
    if (layerLibrary.isEditing(layer)) {
      const { edit_name, edit_dtype, edit_number, edit_dtypenum } = layer;
      
      
      var duplicate;
      if (layerLibrary.checkEditLayer(layer)){
        duplicate = layerLibrary.isDuplicate1(layer) || layerLibrary.isDuplicate2(layer);
      }
      else{
        duplicate = false
      }
      
      if (duplicate) {
        alert('❌ Duplicate layer properties are not allowed.');
        layerLibrary.toggleEdit(layer);
        return;
      }

      // Save
      const updated = {
        layer_name: edit_name,
        layer_number: edit_number,
        datatype_name: edit_dtype,
        datatype_number: edit_dtypenum,
      };

      layerLibrary.updateLayer(layer, updated);
    } else {
      // Prep fields
      layer.edit_name = layer.layer_name;
      layer.edit_number = layer.layer_number;
      layer.edit_dtype = layer.datatype_name;
      layer.edit_dtypenum = layer.datatype_number;
    }
    layerLibrary.toggleEdit(layer);
    forceUpdate((p) => p + 1);
  };




  
  const handleEditInput = (layer, field, value) => {
    layer[field] = value;
    forceUpdate((p) => p + 1);
  };

  const IconButton = ({ icon:Icon, title, onClick }) => (
    <button
      title={title}
      onClick={onClick}
      style={{
        backgroundColor: '#2b2b2b',
        color: '#fff',
        border: '1px solid #444',
        borderRadius: '5px',
        paddingLeft:'2px',
        paddingRight:'2px',
        marginBottom: '2px',
        fontSize: '10px',
        marginRight: '2px',
        // marginBottom: '3px',
        cursor: 'pointer',
      }}
    >
      <Icon style={{ fontSize: '16px' }} />
    </button>
  );

  const renderLayerRow = (layer, i) => {
    const visible = layerLibrary.isLayerVisible(layer.layer_number, layer.datatype_number);
    const locked = layerLibrary.isLayerLocked(layer.layer_number, layer.datatype_number);
    const editing = layerLibrary.isEditing(layer);
    const isSelected = layerLibrary.isLayerSelected(layer);

  
    return (
      <div
        key={i}
        style={{
            ...layerBoxStyle,
            opacity: isSelected ? 0.5 : 1, // 👈 fade if selected
            border: isSelected ? '1px solid #00d8ff' : '1px solid #444', // Optional border
            cursor: 'pointer',
        //     height: editing ? 'auto' : '40px', // Expand height when editing
        // padding: editing ? '12px' : '8px 10px', // Add padding when expanded
        // transition: 'all 0.3s ease', // Smooth transition
        // backgroundColor: editing ? '#333' : '#2b2b2b', // Highlight when editing
        // flexDirection: editing ? 'column' : 'row', // Adjust layout for editing

        transition: 'all 0.3s ease', // Smooth transition
        backgroundColor: editing ? '#333' : '#2b2b2b', // Highlight when editing
        width: editing ? '99%' : '99%', // Increase width when editing
        alignSelf: editing ? 'center' : 'flex-start', // Center the row when editing
          }}

          onClick={(e) => {
            layerLibrary.selectLayer(layer, e.ctrlKey);
            forceUpdate(p => p + 1);
          }}
          
        draggable
        onDragStart={() => setDragIndex(i)}
        onDragOver={(e) => e.preventDefault()}
        
        onDrop={() => {
          if (dragIndex !== null && dragIndex !== i) {
            layerLibrary.reorder(dragIndex, i);
            forceUpdate((p) => p + 1);
          }
        }}
      >
        {/* Color Box */}
        <div
          style={{
            width: '14px',
            height: '14px',
            backgroundColor: layer.color,
            borderRadius: '4px',
            border: '1px solid #444',
            marginRight: '4px',
            marginTop: '2px',
            flexShrink: 0,
          }}
          title={`Layer Name: ${layer.layer_name}
Datatype Name: ${layer.datatype_name}
Layer Number: ${layer.layer_number}
Datatype Number: ${layer.datatype_number}`.trim()} // Tooltip with layer details
        ></div>
  
        {/* Info or Editable Fields */}
        <div style={{ flexGrow: 1, display: 'flex', alignItems: 'center' }}>
        {editing ? (
  <div style={{
    display: 'grid',
    gridTemplateColumns: '50px 50px 30px 30px',
    gap: '1px',
    width: '100%'
  }}>
    <input
      type="text"
      value={layer.edit_name}
      onChange={(e) => handleEditInput(layer, 'edit_name', e.target.value)}
      style={inputStyle}
      placeholder="Name"
    />
    <input
      type="text"
      value={layer.edit_dtype}
      onChange={(e) => handleEditInput(layer, 'edit_dtype', e.target.value)}
      style={inputStyle}
      placeholder="DType"
    />
    <input
      type="number"
      value={layer.edit_number}
      onChange={(e) => handleEditInput(layer, 'edit_number', e.target.value)}
      style={inputStyle}
      placeholder="L#"
    />
    <input
      type="number"
      value={layer.edit_dtypenum}
      onChange={(e) => handleEditInput(layer, 'edit_dtypenum', e.target.value)}
      style={inputStyle}
      placeholder="DT#"
    />
  </div>
          ) : (
            <div style={{
                display: 'grid',
                gridTemplateColumns: '45px 55px 29px 22.5px',
                gap: '2px',
                fontSize: '12px',
                fontWeight: 'bold',
                width: '100%'
              }}>
              

  <div title={layer.layer_name}>{truncateText(layer.layer_name, 3)}</div>
  <div title={layer.datatype_name}>{truncateText(layer.datatype_name, 2)}</div>
  <div title={layer.layer_number.toString()}>{truncateText(layer.layer_number.toString(), 2)}</div>
  <div title={layer.datatype_number.toString()}>{truncateText(layer.datatype_number.toString(), 2)}</div>
</div>

          )}
        </div>
  
        {/* Icons with reduced size */}
        <div style={{ display: 'flex', gap: '0px', alignItems: 'center',gap:'2px',}}>
          <button
            onClick={() => toggleVisibility(layer)}
            style={{
              backgroundColor: visible ? '#2b2b2b' : '#2b2b2b',
              border: 'none',
              borderRadius: '4px',
              width: '16px',
              height: '16px',
              padding: '2px 1px',  // Reduced padding
              cursor: 'pointer',
              fontSize: '10px',  // Reduced font size
              color: '#fff',
            }}
            title="Toggle visibility"
          >
            {visible ? <VisibilityIcon style={{ color: 'white', fontSize:'15px'}} /> : <VisibilityOffIcon style={{ color: 'white', fontSize:'15px'}} />}
          </button>
  
          <button
            onClick={() => toggleLock(layer)}
            style={{
              backgroundColor: locked ? '#2b2b2b' : '#2b2b2b',
              border: 'none',
              borderRadius: '4px',
              width: '16px',
              height: '16px',
              padding: '1px 1px',  // Reduced padding
              cursor: 'pointer',
              fontSize: '10px',  // Reduced font size
              color: '#fff',
            }}
            title="Toggle lock"
          >
            {locked ?<LockIcon style={{ color: 'green', fontSize: '16px' }} /> : <LockOpenIcon style={{ color: 'white', fontSize: '16px' }} />
          }
          </button>
          <button
            onClick={() => toggleEdit(layer)}
            style={{
              backgroundColor: editing ? '#2b2b2b' : '#2b2b2b',
              border: 'none',
              borderRadius: '4px',
              width: '16px',
              height: '16px',
              padding: '2px 2px',  // Reduced padding
              cursor: 'pointer',
              fontSize: '10px',  // Reduced font size
              color: '#fff',
            }}
            title="Edit layer"
          >
            {editing ? <SaveIcon style={{ fontSize: '14px', color:'white'}} />:<EditIcon style={{ fontSize: '14px', color:'white' }} />}
          </button>
        </div>
      </div>
    );
  };
  
  
  

  return (
    <div style={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
      

      {/* 🔧 CONTROL ICON BAR */}
      <div style={iconBarStyle}>
    <IconButton icon={AddIcon} title="Add Layer" onClick={() => {}} />
    <IconButton icon={DownloadIcon} title="Export Layers" onClick={() => {}} />
    <IconButton icon={UploadIcon} title="Import Layers" onClick={() => {}} />
    <IconButton icon={SortByAlphaIcon} title="Sort by Name" onClick={() => { layerLibrary.setSortMode('name'); forceUpdate(p => p + 1); }} />
    <IconButton icon={FormatListNumberedIcon} title="Sort by Number" onClick={() => { layerLibrary.setSortMode('number'); forceUpdate(p => p + 1); }} />
    <IconButton icon={VisibilityIcon} title="Show All" onClick={() => {}} />
    <IconButton icon={VisibilityOffIcon} title="Hide All" onClick={() => {}} />
    <IconButton icon={LockIcon} title="Lock All" onClick={() => {}} />
    <IconButton icon={LockOpenIcon} title="Unlock All" onClick={() => {}} />
  <input
    type="color"
    onChange={(e) => {
      const selected = layerLibrary.getSelectedLayers();
      selected.forEach(layer => layerLibrary.updateColor(layer, e.target.value));
      forceUpdate(p => p + 1);
    }}
    style={{
      marginLeft: '2px',
      marginTop: '0px',
      marginRight: '4px',
      width: '20px', // Reduced width
      height: '20px', // Adjust height to match
      borderRadius: '1px',
      cursor: 'pointer',
      border:'none',
      WebkitAppearance: 'none',
    }}
    title='color picker'
  />
  {/* <label style={{ color: '#aaa', fontSize: '12px',display:'flex',alignItems:'center'}}>Opacity</label> */}
  <input
    type="range"
    min="0.1"
    max="1"
    step="0.1"
    defaultValue="1"
    onChange={(e) => {
      const selected = layerLibrary.getSelectedLayers();
      selected.forEach(layer => layerLibrary.updateOpacity(layer, parseFloat(e.target.value)));
      forceUpdate(p => p + 1);
    }}
    style={{ width: '90px',marginLeft:'2px',marginTop:'0px',marginRight:'4px',cursor:'pointer',backgroundColor:'#444',borderRadius:'2px',border:'none'}}
    title='Opacity'
  />
  <IconButton icon={FlipIcon} title="Invert Visibility" onClick={() => {}} />
  <IconButton icon={GroupWorkIcon} title="Toggle Grouping" onClick={() => setGrouped(prev => !prev)} />
      </div>
      {/* 🔎 SEARCH FIELD */}
      <input
        type="text"
        placeholder="Search..."
        style={searchStyle}
        value={search}
        onChange={(e) => setSearch(e.target.value)}
      />

      {/* 📦 LAYER VIEW */}
      <div style={{ flex: 1,overflowY:'auto', overflowX:'hidden' ,width: '109%'}}>
      {grouped ? (
        Object.entries(groupedLayers).map(([groupName, layers], index) => (
          <div key={index} style={{ marginBottom: '10px' }}>
            <div
              onClick={() => toggleGroup(groupName)}
              style={{
                backgroundColor: '#333',
                padding: '10px',
                cursor: 'pointer',
                fontWeight: 'bold',
                borderRadius: '6px 6px 0 0',
                userSelect: 'none',
              }}
            >
              {expandedGroups[groupName] ? '▼' : '▶'} {groupName}
            </div>

            {expandedGroups[groupName] &&
              layers
                .filter((layer) =>
                  layer.layer_name.toLowerCase().includes(search.toLowerCase())
                )
                .map(renderLayerRow)}
                
          </div>
          
        ))
        
      ) : (
        layers
          .filter((layer) =>
            layer.layer_name.toLowerCase().includes(search.toLowerCase())
          )
          .map(renderLayerRow)
      )}
      </div>
    </div>
  );
};

const iconBarStyle = {
  display: 'flex',
  flexWrap: 'wrap',
  gap: '1px',
  marginBottom: '0px',
  overflowX: 'auto',
  paddingBottom: '2px',
};

const searchStyle = {
  padding: '3px',
  borderRadius: '4px',
  border: '1px solid #555',
  backgroundColor: '#222',
  color: '#fff',
  width: '94%',
  marginBottom: '4px',
  margintop: '0px',
};

const layerBoxStyle = {
  display: 'flex',
  alignItems: 'flex-start',
  backgroundColor: '#2b2b2b',
  padding: '0px 0px',
  margin: '0px 0',
  borderBottom: '1px solid #444',
};

const inputStyle = {
    padding: '0 2px',              // remove vertical padding
    fontSize: '11px',
    height: '20px',                // tighter height
    lineHeight: '20px',            // align text vertically
    borderRadius: '4px',
    border: '1px solid #555',
    backgroundColor: '#1f1f1f',
    color: '#fff',
    width: '100%',
    boxSizing: 'border-box',
  };
  


  // Truncate text to a specific length and add ellipsis
const truncateText = (text, maxLength) => {
    if (text.length > maxLength) {
      return text.substring(0, maxLength) + '...';
    }
    return text;
  };
  
  

export default LayerTab;
