  setGridSpacing(spacing) {
    this.gridSpacing = spacing;
    this.draw();
  }

  drawLabels() {
    const ctx = this.ctx;
    ctx.save();
    ctx.translate(this.offsetX, this.offsetY);
    ctx.scale(this.scale, this.scale);
    const labels = labelLibrary.getAll();
    for (let label of labels) {
      if (!labelLibrary.isVisible(label)) continue;
      ctx.fillStyle = 'yellow';
      ctx.font = '12px monospace';
      ctx.fillText(label.text, label.x, label.y);
    }
    ctx.restore();
  }

  startMarquee(e) { 
    const currentTime = Date.now();
    const isDoubleClick = (currentTime - this.lastClickTime < 300);
    this.lastClickTime = currentTime;

    const x = (e.offsetX - this.offsetX) / this.scale;
    const y = (e.offsetY - this.offsetY) / this.scale;

    this.selectionDragStart = { x, y };
    this.selectionBox = null;
    this.isSelecting = false;
    this.leftClickMarquee = (e.button === 0);
    this.doubleClickOnly = isDoubleClick;
}

  updateMarquee(e) { 
    if ((this.leftClickMarquee || this.doubleClickOnly) && ToolManager.getCurrentTool() === 'select' && !this.isDragging) {
    const currX = (e.offsetX - this.offsetX) / this.scale;
    const currY = (e.offsetY - this.offsetY) / this.scale;

    const dx = Math.abs(currX - this.selectionDragStart.x);
    const dy = Math.abs(currY - this.selectionDragStart.y);

    if (!this.isSelecting && (dx > 4 || dy > 4)) {
      this.selectionBox = {
        x: this.selectionDragStart.x,
        y: this.selectionDragStart.y,
        width: 0,
        height: 0
      };
      this.isSelecting = true;
    }

    if (this.isSelecting && this.selectionBox) {
      this.selectionBox.width = currX - this.selectionBox.x;
      this.selectionBox.height = currY - this.selectionBox.y;
      this.draw();
    }
  }
}

