finishMarquee(e) { 
  if (this.isSelecting && this.selectionBox && ToolManager.getCurrentTool() === 'select') {
    const { x, y, width, height } = this.selectionBox;
    const x1 = Math.min(x, x + width);
    const y1 = Math.min(y, y + height);
    const x2 = Math.max(x, x + width);
    const y2 = Math.max(y, y + height);

    const cellName = ProjectManager.getSelectedCell();
    const rectLayers = InstanceLibrary.getAllInstances()[cellName] || {};
    const allRects = Object.values(rectLayers).flat();
    const allLabels = labelLibrary.getAllLabels(cellName);
    const currentCell = layoutEngine.getCellByName(cellName);
    const instances = currentCell?.instances || [];

    const selectedRects = allRects.filter(rect =>
      rect.visible !== false && // Skip invisible rectangles
      rect.x >= x1 && rect.x + rect.width <= x2 &&
      rect.y >= y1 && rect.y + rect.height <= y2
    );

    const selectedLabels = allLabels.filter(label =>
      label.visible !== false && // Skip invisible labels
      label.x >= x1 && label.x <= x2 &&
      label.y >= y1 && label.y <= y2
    );

    const selectedInstances = instances.filter(inst => {
      const refCell = layoutEngine.getCellByName(inst.ref);
      if (!refCell) return false;

      const { offsetX: ox, offsetY: oy } = this.getChildBoundingBoxCenterOffset(refCell);

      for (let el of refCell.elements) {
        if (el.type !== "rectangle") continue;

        const elX = inst.x + ox + el.x;
        const elY = inst.y + oy + el.y;
        const width = el.width;
        const height = el.height;

        if (
          elX >= x1 && elX + width <= x2 &&
          elY >= y1 && elY + height <= y2
        ) {
          return true;
        }
      }
      return false;
    });

    const combined = [
      ...selectedRects.map(r => ({ id: r.id, type: 'rectangle', ref: r })),
      ...selectedLabels.map(l => ({ id: l.id, type: 'label', ref: l })),
      ...selectedInstances.map(inst => ({ id: inst.id, type: 'instance', ref: inst }))
    ];

    this.selectedRects = selectedRects;
    this.selectedObjects = combined;
    selectionLibrary.setSelection(combined);
    this.draw();
  }

  this.selectionBox = null;
  this.isSelecting = false;
  this.selectionDragStart = null;
  this.leftClickMarquee = false;
  this.doubleClickOnly = false;
}


drawSelectionMarquee() {
  if (!(this.selectionBox && this.isSelecting)) return;

  const { x, y, width, height } = this.selectionBox;
  const ctx = this.ctx;

  ctx.save();
  ctx.translate(this.offsetX, this.offsetY);
  ctx.scale(this.scale, this.scale);
  ctx.strokeStyle = "#0077ff";
  ctx.setLineDash([6]);
  ctx.strokeRect(x, y, width, height);
  ctx.setLineDash([]);
  ctx.restore();
}

