  //     let newRotation = (rect.rotation || 0) + degrees;
  //     newRotation = ((newRotation % 360) + 360) % 360;
  //     rect.rotation = newRotation;
  
  //     // swap width/height if 90 or 270
  //     if (newRotation === 90 || newRotation === 270) {
