  resetToolStates() {
    this.isDrawing = false;
    this.isDragging = false;
    this.previewInstance = null;
  
    if (this.rectangleHandler?.cancelDrawing) {
      this.rectangleHandler.cancelDrawing();
    }
    if (this.labelHandler?.cancelLabeling) {
      this.labelHandler.cancelLabeling();
    }
  }


//============================================================================================================================================
// 📋 Common Reusable Operation Functions



snapToGrid(value) {
  const spacing = this.gridSpacing || 20;
  return Math.round(value / spacing) * spacing;
}







  handleSelectObject(e) {
  if (this.isMultiMoveMode) return;

  const x = (e.offsetX - this.offsetX) / this.scale;
  const y = (e.offsetY - this.offsetY) / this.scale;

  const ctrlKey = e.ctrlKey || e.metaKey;

  // const allRects = Object.values(InstanceLibrary.getAllInstances()).flat();
  // const allLabels = labelLibrary.getAllLabels();
  // shiv code
  const cellName = ProjectManager.getSelectedCell();
  const cellData = InstanceLibrary.getAllInstances()[cellName] || {};
  const allRects = Object.values(cellData).flat();  // 🔁 Flatten all layers in this cell only
  const allLabels = labelLibrary.getAllLabels(cellName);
  // end shiv

  let clickedObject = null;

  // ✅ First check LABELS (priority to top-most elements)
  for (let i = allLabels.length - 1; i >= 0; i--) {
    const label = allLabels[i];
    if (label.visible === false) continue;


    this.ctx.font = `${label.fontSize}px monospace`;
    const width = Math.max(this.ctx.measureText(label.text).width, 20);
    const height = label.fontSize;

    
   let boxX = label.x;
let boxY = label.y;

switch (label.justification) {
  case 'upperleft':
    // no change
    break;
  case 'upperright':
    boxX -= width;
    break;
  case 'lowerleft':
    boxY -= height;
    break;
  case 'lowerright':
    boxX -= width;
    boxY -= height;
    break;
  case 'center':
    boxX -= width / 2;
    boxY -= height / 2;
    break;
  case 'centerleft':
    boxY -= height / 2;
    break;
  case 'centerright':
    boxX -= width;
    boxY -= height / 2;
    break;
}

// Final bounds
const left = boxX - 4;
const top = boxY - 2;
const right = boxX + width + 4;
const bottom = boxY + height + 6; // buffer for vertical padding

if (
  x >= left &&
  x <= right &&
  y >= top &&
  y <= bottom
) {
  if (!label.locked) {
    clickedObject = { id: label.id, type: 'label', ref: label };
    break;
  }
}

  }

  // ✅ Only check RECTANGLES if no label was clicked
  if (!clickedObject) {
    for (let i = allRects.length - 1; i >= 0; i--) {
      const rect = allRects[i];
      // Skip invisible rectangles
      if (rect.visible === false) continue;
      const rectLeft = Math.min(rect.x, rect.x + rect.width);
      const rectRight = Math.max(rect.x, rect.x + rect.width);
      const rectTop = Math.min(rect.y, rect.y + rect.height);
      const rectBottom = Math.max(rect.y, rect.y + rect.height);

    if (x >= rectLeft && x <= rectRight && y >= rectTop && y <= rectBottom) {
      clickedObject = { id: rect.id, type: 'rectangle', ref: rect };
      break;
    }

    }
  }

    // ✅ Check placed instances // shiv code if block
  if (!clickedObject) {
  const cellName = ProjectManager.getSelectedCell();
  const currentCell = layoutEngine.getCellByName(cellName);
  const instances = currentCell?.instances || [];

  for (let i = instances.length - 1; i >= 0; i--) {
    const inst = instances[i];
    const refCell = layoutEngine.getCellByName(inst.ref);
    if (!refCell) continue;

    const { offsetX: childOffsetX, offsetY: childOffsetY } = this.getChildBoundingBoxCenterOffset(refCell);

    for (let el of refCell.elements) {
      if (el.type !== "rectangle") continue;

      const elX = inst.x + childOffsetX + el.x;
      const elY = inst.y + childOffsetY + el.y;
      const width = el.width;
      const height = el.height;

      if (x >= elX && x <= elX + width && y >= elY && y <= elY + height) {
        clickedObject = { id: inst.id, type: 'instance', ref: inst };
        break;
      }
    }

    if (clickedObject) break;
   }
 }

  // ✅ Selection handling
  if (clickedObject) {
    const alreadySelected = this.selectedObjects.find(
      obj => obj.type === clickedObject.type && obj.id === clickedObject.id
    );

    if (ctrlKey) {
      if (alreadySelected) {
        // Ctrl + click to unselect
        this.selectedObjects = this.selectedObjects.filter(
          obj => !(obj.type === clickedObject.type && obj.id === clickedObject.id)
        );
      } else {
        // Ctrl + click to add to selection
        this.selectedObjects.push(clickedObject);
      }
    } else {
      // Normal click: select single object
      this.selectedObjects = [clickedObject];
    }

    // selectionLibrary.setSelection(this.selectedObjects.map(obj => obj.ref));

    // shiv code
    selectionLibrary.setSelection(this.selectedObjects);
    //end shiv code
  } else if (!ctrlKey) {
    // Clicked blank area without ctrl → clear selection
    this.selectedObjects = [];
    selectionLibrary.clearSelection();
  }

  this.draw();
  }

  
  copySelectedObjects() {
    if (!this.selectedObjects?.length) return;
  
    this.copiedObjects = this.selectedObjects.map(obj => ({
      id: obj.id,
      type: obj.type,
      ref: JSON.parse(JSON.stringify(obj.ref)) // Deep copy to avoid live link
    }));
  
    console.log("📋 Copied selected objects");
  }
  
// shiv new code
  pasteCopiedObjects() {
  this.recordState();
  if (!this.copiedObjects?.length || !this.lastClickForPaste) return;

  const base = this.copiedObjects[0];
  const offsetX = this.lastClickForPaste.x - base.ref.x;
  const offsetY = this.lastClickForPaste.y - base.ref.y;

  const newObjects = [];
  const cellName = ProjectManager.getSelectedCell();// shiv 

  for (let obj of this.copiedObjects) {
    if (obj.type === 'rectangle') {
      const newId = Date.now() + Math.random();

      const x = this.snapToGrid(obj.ref.x + offsetX);
      const y = this.snapToGrid(obj.ref.y + offsetY);

      const newRect = {
        ...obj.ref,
        id: newId,
        x,
        y,
        selected: false
      };

      InstanceLibrary.addRectangle(
        cellName,
        newRect.layerId,
        x,
        y,
        newRect.width,
        newRect.height,
        newRect
      );

      // ✅ Get actual live reference from the library
      const rectsInLayer = InstanceLibrary.getAllInstances()[newRect.layerId] || [];
      const liveRef = rectsInLayer.find(r => r.id === newId);

      if (liveRef) {
        newObjects.push({ id: newId, type: 'rectangle', ref: liveRef });
      }

    } else if (obj.type === 'label') {
      const x = this.snapToGrid(obj.ref.x + offsetX);
      const y = this.snapToGrid(obj.ref.y + offsetY);
      const newLabel = labelLibrary.addLabel(
        cellName,
        obj.ref.layerId,
        x,
        y,
        obj.ref.text,
        obj.ref.color
      );
      newObjects.push({ id: newLabel.id, type: 'label', ref: newLabel });
    }
    //shivcode
    else if (obj.type === 'instance') {
        const newInst = {
          ...obj.ref,
          id: Date.now() + Math.random(),
          x: this.snapToGrid(obj.ref.x + offsetX),
          y: this.snapToGrid(obj.ref.y + offsetY),
        };
      
        layoutEngine.addInstance(cellName, newInst.ref, newInst.x, newInst.y);
      
        newObjects.push({ id: newInst.id, type: 'instance', ref: newInst });
      }
  }

  this.selectedObjects = newObjects;
  selectionLibrary.setSelection(newObjects);
  this.draw();

  console.log("📥 Pasted copied objects and fixed live references");
}
// end shiv code

