// shiv updated functions
  restoreState(state) {
  InstanceLibrary.setAllInstances(state.rectangles);
  labelLibrary.setAllLabels(state.labels);

  const selectedIds = state.selectedIds || [];

  const allRects = Object.values(state.rectangles || {})
    .flatMap(layerMap => Object.values(layerMap).flat());

  const allLabels = Object.values(state.labels || {})
    .flatMap(layerMap => Object.values(layerMap).flat());

  const selectedRects = allRects.filter(r => selectedIds.includes(r.id));
  const selectedLabels = allLabels.filter(l => selectedIds.includes(l.id));

  this.selectedObjects = [
    ...selectedRects.map(r => ({ id: r.id, type: "rectangle", ref: r })),
    ...selectedLabels.map(l => ({ id: l.id, type: "label", ref: l }))
  ];

  selectionLibrary.setSelection(this.selectedObjects);
  this.draw();
}

undo() {
  if (this.history.length === 0) return;

  // ✅ Save current state (with nested structure)
  const currentState = {
    rectangles: JSON.parse(JSON.stringify(InstanceLibrary.getAllInstances())),
    labels: JSON.parse(JSON.stringify(labelLibrary.getAllLabelMap())), // ✅ nested
    selectedIds: this.selectedObjects.map(obj => obj.id)
  };
  this.future.push(currentState);

  const prevState = this.history.pop();
  this.restoreState(prevState);

  // ✅ Flatten nested structures to rebuild selection
  const selectedIds = prevState.selectedIds || [];

  const allRects = Object.values(prevState.rectangles || {})
    .flatMap(layerMap => Object.values(layerMap).flat());

  const allLabels = Object.values(prevState.labels || {})
    .flatMap(layerMap => Object.values(layerMap).flat());

  const selectedRects = allRects.filter(r => selectedIds.includes(r.id));
  const selectedLabels = allLabels.filter(l => selectedIds.includes(l.id));

  this.selectedObjects = [
    ...selectedRects.map(r => ({ id: r.id, type: "rectangle", ref: r })),
    ...selectedLabels.map(l => ({ id: l.id, type: "label", ref: l }))
  ];

  selectionLibrary.setSelection(this.selectedObjects);
  this.draw();
  console.log("↩️ Undo");
}

redo() {
  if (this.future.length === 0) return;

  const currentState = {
    rectangles: JSON.parse(JSON.stringify(InstanceLibrary.getAllInstances())),
    labels: JSON.parse(JSON.stringify(labelLibrary.getAllLabelMap())), // ✅ nested
    selectedIds: this.selectedObjects.map(obj => obj.id)
  };
  this.history.push(currentState);

  const nextState = this.future.pop();
  this.restoreState(nextState);

  const selectedIds = nextState.selectedIds || [];

  const allRects = Object.values(nextState.rectangles || {})
    .flatMap(layerMap => Object.values(layerMap).flat());

  const allLabels = Object.values(nextState.labels || {})
    .flatMap(layerMap => Object.values(layerMap).flat());

  const selectedRects = allRects.filter(r => selectedIds.includes(r.id));
  const selectedLabels = allLabels.filter(l => selectedIds.includes(l.id));

  this.selectedObjects = [
    ...selectedRects.map(r => ({ id: r.id, type: "rectangle", ref: r })),
    ...selectedLabels.map(l => ({ id: l.id, type: "label", ref: l }))
  ];

  selectionLibrary.setSelection(this.selectedObjects);
  this.draw();
  console.log("↪️ Redo");
}
 
