performResize(e) {
  if (!this.isResizing || !this.startResizeRects) return;
  // Check if any selected rectangle is locked
  const hasLocked = this.selectedObjects.some(obj => 
    obj.type === 'rectangle' && obj.ref.locked
  );
  if (hasLocked) {
    console.log("🔒 Cannot resize locked elements");
    this.isResizing = false;
    return;
  }

  const x = (e.offsetX - this.offsetX) / this.scale;
  const y = (e.offsetY - this.offsetY) / this.scale;
  const deltaX = this.snapToGrid(x - this.startResizePos.x);
  const deltaY = this.snapToGrid(y - this.startResizePos.y);

  for (let info of this.startResizeRects) {
    const obj = this.selectedObjects.find(o => o.id === info.id);
    if (!obj) continue;

    // --- 🧱 Handle Rectangle Resize ---
    if (obj.type === "rectangle") {
      const rect = obj.ref;
      // Reset to original
      rect.x = info.x;
      rect.y = info.y;
      rect.width = info.width;
      rect.height = info.height;

      this.applyResize(rect, deltaX, deltaY, this.resizeEdge, info);
    }

    // --- 🧩 Handle Instance Scaling ---
    else if (obj.type === "instance" && obj.ref) {
      const inst = obj;
      const refCell = layoutEngine.getCellByName(inst.ref);
      if (!refCell) continue;

      const { offsetX, offsetY } = this.getChildBoundingBoxCenterOffset(refCell);

      let minX = Infinity, minY = Infinity, maxX = -Infinity, maxY = -Infinity;
      for (let el of refCell.elements) {
        if (el.type !== "rectangle") continue;
        const elX = offsetX + el.x;
        const elY = offsetY + el.y;
        minX = Math.min(minX, elX);
        minY = Math.min(minY, elY);
        maxX = Math.max(maxX, elX + el.width);
        maxY = Math.max(maxY, elY + el.height);
      }

      const boxWidth = maxX - minX;
      const boxHeight = maxY - minY;

      const rawScaleX = (x - inst.x) / boxWidth;
      const rawScaleY = (y - inst.y) / boxHeight;

      const snappedWidth = this.snapToGrid(rawScaleX * boxWidth);
      const snappedHeight = this.snapToGrid(rawScaleY * boxHeight);

      const scaleX = snappedWidth / boxWidth;
      const scaleY = snappedHeight / boxHeight;

      const newScale = Math.max(0.05, Math.min(scaleX, scaleY)); // uniform scale
      inst.scale = newScale;
    }
  }

  this.draw();
}

// 🔧 Helper function to apply edge-based rectangle resizing
applyResize(rect, dx, dy, edge, original) {
  switch (edge) {
    case "left":
      rect.x = original.x + dx;
      rect.width = original.width - dx;
      break;
    case "right":
      rect.width = original.width + dx;
      break;
    case "top":
      rect.y = original.y + dy;
      rect.height = original.height - dy;
      break;
    case "bottom":
      rect.height = original.height + dy;
      break;
    case "top-left":
      rect.x = original.x + dx;
      rect.width = original.width - dx;
      rect.y = original.y + dy;
      rect.height = original.height - dy;
      break;
    case "top-right":
      rect.width = original.width + dx;
      rect.y = original.y + dy;
      rect.height = original.height - dy;
      break;
    case "bottom-left":
      rect.x = original.x + dx;
      rect.width = original.width - dx;
      rect.height = original.height + dy;
      break;
    case "bottom-right":
      rect.width = original.width + dx;
      rect.height = original.height + dy;
      break;
  }
}




  endResize() {
  if (!this.resizeTarget) return;

  // ✅ Normalize rectangle dimensions after resize ends
  if (this.resizeTarget.width < 0) {
    this.resizeTarget.x += this.resizeTarget.width;
    this.resizeTarget.width = Math.abs(this.resizeTarget.width);
  }

  if (this.resizeTarget.height < 0) {
    this.resizeTarget.y += this.resizeTarget.height;
    this.resizeTarget.height = Math.abs(this.resizeTarget.height);
  }

  this.isResizing = false;
  this.resizeTarget = null;
  this.resizeEdge = null;
  this.startResizeRect=null;
}

  










  
  updateSelectedProperty(propertyName, value) {
    if (!this.selectedObjects.length) return;
  
    for (let obj of this.selectedObjects) {
      if (propertyName === 'layerId') {
        // ✅ Move object to another layer
        if (obj.type === 'rectangle') {
          // Remove from old layer
          InstanceLibrary.deleteRectangle(obj.ref.layerId, obj.ref.id);
          // Update layerId
          obj.ref.layerId = value;
          // Re-add to new layer
          InstanceLibrary.addRectangle(obj.ref.layerId, obj.ref.x, obj.ref.y, obj.ref.width, obj.ref.height, obj.ref);
        } else if (obj.type === 'label') {
          // Remove from old layer
          labelLibrary.deleteLabel(obj.ref.layerId, obj.ref.id);
          // Update layerId
          obj.ref.layerId = value;
          // Re-add to new layer
          labelLibrary.addLabel(obj.ref.layerId, obj.ref.x, obj.ref.y, obj.ref.text, obj.ref.color);
        }
      } else if (obj.ref.hasOwnProperty(propertyName)) {
        obj.ref[propertyName] = value;
      }
    }
  
    this.draw();
    console.log(`✅ Updated ${propertyName} to ${value}`);
  }

  // In CanvasEngine.js - update the method
  updateSelectedPropertyById(id, type, key, value) {
  const target = this.selectedObjects.find(obj => obj.id === id && obj.type === type);
  if (!target || !target.ref) return;

  // Special handling for layer changes
  if (key === 'layerId') {
    this.updateSelectedLayer(value);
    return;
  }

  // Normal property update
  target.ref[key] = value;
  this.draw();
  console.log(`✅ Updated ${key} for ${type} ID ${id} to ${value}`);
  }

  // In CanvasEngine.js - update or add this method
  updateSelectedLayer(newLayerId) {
  this.recordState();
  if (!this.selectedObjects?.length) return;

  const cellName = ProjectManager.getSelectedCell();
  const success = layerLibrary.updateObjectsLayer(this.selectedObjects, newLayerId, cellName);

  if (success) {
    this.draw();
    console.log(`🔄 Updated selected objects to layer ${newLayerId}`);
  }
  }
  

getSelectedProperties() {
  if (!this.selectedObjects || !this.selectedObjects.length) return [];

  return this.selectedObjects.map(obj => ({
    id: obj.id,
    type: obj.type,
    properties: { ...obj.ref } // deep copy of properties
  }));
}


  
  





focusOnLabel(labelId) {
  const label = labelLibrary.getLabelById(labelId);
  if (label) {
    const labelX = label.x;
    const labelY = label.y;

    // 1️⃣ zoom in karo (maan lo default zoom 1 hai):
    this.scale = 2; // ya jo bhi zoom factor chahiye

    // 2️⃣ center ko adjust karo
    this.offsetX = -labelX * this.scale + this.canvas.width / 2;
    this.offsetY = -labelY * this.scale + this.canvas.height / 2;

    this.draw(); // redraw canvas
  }
}









  
