  drawAll: (ctx, offsetX, offsetY, scale, activeCellName, previewRect) => {
    ctx.save();
    ctx.translate(offsetX, offsetY);
    ctx.scale(scale, scale);
  
    const cellLayers = instances[activeCellName] || {};
    const hasInstances = Object.keys(cellLayers).some(layerId => cellLayers[layerId]?.length > 0);
    
    if (hasInstances) {
      for (const layerId in cellLayers) {
        const items = cellLayers[layerId];
        for (const inst of items) {
          ctx.beginPath();
          ctx.fillStyle = inst.fill;
          ctx.strokeStyle = inst.stroke;
          ctx.lineWidth = 1 / scale;
  
          const cx = inst.x + inst.width / 2;
          const cy = inst.y + inst.height / 2;
  
          ctx.save();
          ctx.translate(cx, cy);
          ctx.rotate((inst.rotation || 0) * Math.PI / 180);
          ctx.translate(-cx, -cy);
  
          ctx.rect(inst.x, inst.y, inst.width, inst.height);
          ctx.fill();
          ctx.stroke();
  
          ctx.restore();
        }
      }
    } else {
      const cell = layoutEngine.getCellByName(activeCellName);
      if (cell && cell.elements?.length) {
        for (const el of cell.elements) {
          if (el.type === "rectangle") {
            let x = el.x1 ?? el.x;
            let y = el.y1 ?? el.y;
            let width = el.x2 ? el.x2 - el.x1 : el.width;
            let height = el.y2 ? el.y2 - el.y1 : el.height;
  
            const layer = layerLibrary.getLayerById(`${el.layer}:${el.datatype ?? 0}`);
            const fill = layer?.color || "rgba(0,255,0,0.3)";
            const stroke = layer?.color || "green";
  
            ctx.beginPath();
            ctx.fillStyle = fill;
            ctx.strokeStyle = stroke;
            ctx.lineWidth = 1 / scale;
  
            const cx = x + width / 2;
            const cy = y + height / 2;
  
            ctx.save();
            ctx.translate(cx, cy);
            ctx.rotate((el.rotation || 0) * Math.PI / 180);
            ctx.translate(-cx, -cy);
  
            ctx.rect(x, y, width, height);
            ctx.fill();
            ctx.stroke();
  
            ctx.restore();
          }
        }
      }
    }
  
    if (previewRect) {
      ctx.beginPath();
      ctx.setLineDash([4, 2]);
      ctx.strokeStyle = "#fff";
      ctx.lineWidth = 1 / scale;
      ctx.rect(previewRect.x, previewRect.y, previewRect.width, previewRect.height);
      ctx.stroke();
      ctx.setLineDash([]);
    }
  
    ctx.restore();
  },  
