// src/panels/LabelPanel.jsx

import React, { useState, useEffect } from 'react';
import labelLibrary from '../libraries/LabelLibrary';

// MUI icons
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import LockIcon from '@mui/icons-material/Lock';
import LockOpenIcon from '@mui/icons-material/LockOpen';
import SearchIcon from '@mui/icons-material/Search';
import DeleteSweepIcon from '@mui/icons-material/DeleteSweep';

const LabelPanel = () => {
  const [labels, setLabels] = useState(labelLibrary.getAllLabels());  // 🟢 initial fetch

const forceRefresh = () => setLabels(labelLibrary.getAllLabels());  // 🟢 refresh method

const [isEditing, setIsEditing] = useState(false);


  const [editingLabelId, setEditingLabelId] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [allVisible, setAllVisible] = useState(true);
  const [allLocked, setAllLocked] = useState(false);

 

  useEffect(() => {
    if (isEditing) return;  // agar edit ho raha, refresh na kare
  
    const interval = setInterval(() => {
      setLabels(labelLibrary.getAllLabels());
    }, 500);
  
    return () => clearInterval(interval);
  }, [isEditing]);
  

  const handleEditStart = (labelId) => {
    setEditingLabelId(labelId);
    setIsEditing(true);
  };

  const handleEditChange = (e, labelId) => {
    const updatedText = e.target.value;
    const updatedLabels = labels.map(label => {
      if (label.id === labelId) {
        return { ...label, text: updatedText };
      }
      return label;
    });
    setLabels(updatedLabels);
  };
  

  const handleDelete = (labelId) => {
    const label = labels.find(l => l.id === labelId);
    if (label) {
      labelLibrary.deleteLabel(label.layerId, label.id);
      forceRefresh();
    }
  };
  
  const toggleVisibility = (labelId) => {
    labelLibrary.toggleLabelVisibility(labelId);
    forceRefresh();
  };
  
  const toggleLock = (labelId) => {
    labelLibrary.toggleLabelLock(labelId);
    forceRefresh();
  };
  
  const toggleAllVisibility = () => {
    labelLibrary.toggleAllVisibility(!allVisible);
    setAllVisible(!allVisible);
    forceRefresh();
  };
  
  const toggleAllLock = () => {
    labelLibrary.toggleAllLock(!allLocked);
    setAllLocked(!allLocked);
    forceRefresh();
  };
  
  const handleDeleteAll = () => {
    labelLibrary.deleteAllLabels();
    forceRefresh();
  };
  
  const handleEditSave = (labelId) => {
    const label = labels.find(l => l.id === labelId);
    if (label) {
      labelLibrary.updateLabelText(labelId, label.text);
      setEditingLabelId(null);
      setIsEditing(false); // ✅ editing khatam
      // forceRefresh(); --> ab zarurat nahi
    }
  };
  
  
  
  const handleEditKeyDown = (e, labelId) => {
    if (e.key === 'Enter') {
      handleEditSave(labelId);
    }
  };
  

  const filteredLabels = labels.filter(label =>
    label.text.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div style={{ padding: '12px', overflowY: 'auto', maxHeight: '100%', background: '#1c1c1c', color: '#fff' }}>
      <h3 style={{ marginBottom: '12px', fontWeight: '600' }}>Labels</h3>

      {/* 🔍 Search box */}
      <div style={{ display: 'flex', alignItems: 'center', marginBottom: '8px', background: '#2a2a2a', borderRadius: '4px', padding: '4px 8px' }}>
        <SearchIcon style={{ marginRight: '6px', fontSize: '20px', color: '#aaa' }} />
        <input
          type="text"
          placeholder="Search label..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          style={{
            flexGrow: 1,
            background: 'transparent',
            border: 'none',
            outline: 'none',
            color: '#fff',
            fontSize: '14px'
          }}
        />
      </div>

      {/* 👁️ 🔒 🗑️ action icons row */}
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '12px' }}>
        <VisibilityIcon
          onClick={toggleAllVisibility}
          style={{ cursor: 'pointer', fontSize: '20px', color: '#ccc' }}
          titleAccess="Toggle All Visibility"
        />
        <LockIcon
          onClick={toggleAllLock}
          style={{ cursor: 'pointer', fontSize: '20px', color: '#ccc' }}
          titleAccess="Toggle All Lock"
        />
        <DeleteSweepIcon
          onClick={handleDeleteAll}
          style={{ cursor: 'pointer', fontSize: '20px', color: '#ccc' }}
          titleAccess="Delete All Labels"
        />
      </div>

      {filteredLabels.length === 0 && <p style={{ color: '#888', fontSize: '13px' }}>No labels found</p>}
      {filteredLabels.map((label) => (
        <div
          key={label.id}
          style={{
            display: 'flex',
            alignItems: 'center',
            marginBottom: '8px',
            background: '#2a2a2a',
            borderRadius: '4px',
            padding: '6px 8px',
            transition: 'background 0.2s'
          }}
          onClick={() => window.canvasEngine && window.canvasEngine.focusOnLabel && window.canvasEngine.focusOnLabel(label.id)}

          onMouseEnter={(e) => {
            e.currentTarget.querySelectorAll('svg').forEach(icon => icon.style.fontSize = '16px');
            e.currentTarget.style.background = '#333';
          }}
          onMouseLeave={(e) => {
            e.currentTarget.querySelectorAll('svg').forEach(icon => icon.style.fontSize = '18px');
            e.currentTarget.style.background = '#2a2a2a';
          }}
        >
          {editingLabelId === label.id ? (
            <input
              type="text"
              value={label.text}
              onChange={(e) => handleEditChange(e, label.id)}
              onKeyDown={(e) => handleEditKeyDown(e, label.id)}
              onBlur={() => handleEditSave(label.id)}
              autoFocus
              style={{
                flexGrow: 1,
                background: '#444',
                border: 'none',
                outline: 'none',
                color: '#fff',
                fontSize: '13px',
                padding: '2px 4px',
                borderRadius: '4px'
              }}
            />
          ) : (
            <span style={{ flexGrow: 1, fontSize: '13px', whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis' }}>{label.text}</span>
          )}
          <EditIcon onClick={() => handleEditStart(label.id)} style={{ cursor: 'pointer', marginRight: '6px', fontSize: '18px' }} />
          <DeleteIcon onClick={() => handleDelete(label.id)} style={{ cursor: 'pointer', marginRight: '6px', fontSize: '18px' }} />
          {label.visible !== false ? (
            <VisibilityIcon onClick={() => toggleVisibility(label.id)} style={{ cursor: 'pointer', marginRight: '6px', fontSize: '18px' }} />
          ) : (
            <VisibilityOffIcon onClick={() => toggleVisibility(label.id)} style={{ cursor: 'pointer', marginRight: '6px', fontSize: '18px' }} />
          )}
          {label.locked ? (
            <LockIcon onClick={() => toggleLock(label.id)} style={{ cursor: 'pointer', fontSize: '18px' }} />
          ) : (
            <LockOpenIcon onClick={() => toggleLock(label.id)} style={{ cursor: 'pointer', fontSize: '18px' }} />
          )}
        </div>
      ))}
    </div>
  );
};

export default LabelPanel;
