import React, { useRef, useState, useEffect } from 'react';
import axios from 'axios';
import CanvasBoard from './components/CanvasBoard';
import RightPanel from './panels/RightPanel';
import StartModal from './components/StartModal';
import TopBar from './components/top';
import LeftBar from './components/leftbar';
import './components/top.css';
import './components/leftbar.css';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';

function App() {
  const [layoutStarted, setLayoutStarted] = useState(false);
  const [isRightPanelVisible, setIsRightPanelVisible] = useState(false);
  const [verified, setVerified] = useState(null); // null = loading
  const [error, setError] = useState(null);
  const canvasRef = useRef(null);

  const API_BASE_URL = process.env.REACT_APP_API_BASE_URL;

  useEffect(() => {
    const API_BASE_URL = process.env.REACT_APP_API_BASE_URL;
    const urlParams = new URLSearchParams(window.location.search);
    const urlToken = urlParams.get('token');
    const accessToken = localStorage.getItem('access_token');

    const cleanURL = () => {
      window.history.replaceState({}, document.title, window.location.pathname);
    };

    const verifyWithUrlToken = async () => {
      try {
        console.log('🔄 Verifying with URL token...');
        const res = await axios.post(`${API_BASE_URL}/auth/verify-token`, { token: urlToken }, {
          withCredentials: true
        });
        console.log('✅ URL token verified:', res.data);
        localStorage.setItem('access_token', res.data.access_token);
        localStorage.setItem('verified', 'true');
        setVerified(true);
        cleanURL();
      } catch (err) {
        console.error('❌ URL token verification error:', err);
        setError(err.response?.data?.msg || 'Token verification failed');
        setVerified(false);
      }
    };


    const verifyWithAccessToken = async () => {
      try {
        console.log('🔄 Verifying with access token (in body)...');
        const res = await axios.post(`${API_BASE_URL}/auth/verify-token`, {
          token: accessToken,
        });
        console.log('✅ Access token verified:', res.data);
        setVerified(true);
      } catch (err) {
        console.error('❌ Access token error:', err);
        if (err.response?.status === 401) {
          console.log('⚠️ Access token expired. Trying refresh...');
          await refreshAccessToken();
        } else {
          setError(err.response?.data?.msg || 'Invalid access token');
          setVerified(false);
        }
      }
    };


    const refreshAccessToken = async () => {
      try {
        const res = await axios.post(`${API_BASE_URL}/auth/refresh`, {}, {
          withCredentials: true,
        });
        console.log('✅ Token refreshed:', res.data);
        localStorage.setItem('access_token', res.data.access_token);
        setVerified(true);
      } catch (err) {
        console.error('❌ Refresh failed:', err);
        setError('Session expired. Please login again.');
        setVerified(false);
      }
    };


    const runVerification = async () => {
      try {
        if (urlToken) {
          await verifyWithUrlToken();
        } else if (accessToken) {
          await verifyWithAccessToken();
        } else {
          console.error('🚫 No token found');
          setError('No token provided');
          setVerified(false);
        }
      } catch (e) {
        console.error('❌ Unexpected error in runVerification:', e);
        setError('Unexpected verification error');
        setVerified(false);
      }
    };

    runVerification();
  }, []);


  const handleStartNew = () => {
    setLayoutStarted(true);
  };

  const handleLoadJSON = (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (event) => {
      try {
        const data = JSON.parse(event.target.result);
        setLayoutStarted(true);
        setTimeout(() => {
          if (window.canvasEngine) {
            window.canvasEngine.loadFromJSON(data);
          }
        }, 100);
      } catch (err) {
        alert("Invalid JSON file");
      }
    };
    reader.readAsText(file);
  };

  if (verified === null) {
    return <div style={{ padding: 20 }}>Verifying token, please wait...</div>;
  }

  if (!verified) {
    return <div style={{ color: 'red', padding: 20 }}>Error: {error}</div>;
  }

  return (
    <div style={{ display: 'flex', flexDirection: 'column', height: '100vh' }}>
      <TopBar />
      <div style={{ display: 'flex', flex: 1 }}>
        <LeftBar />
        <div style={{ flex: 1, position: 'relative', display: 'flex' }}>
          <CanvasBoard ref={canvasRef} />
          {isRightPanelVisible && <RightPanel />}
          <button
            onClick={() => setIsRightPanelVisible(!isRightPanelVisible)}
            style={{
              position: 'absolute',
              top: "0px",
              right: isRightPanelVisible ? '253px' : '3px',
              zIndex: 1000,
              padding: '0px 0px',
              backgroundColor: '#2b2b2b',
              color: '#fff',
              border: '1px solid #444',
              borderRadius: '4px',
              cursor: 'pointer',
              transition: 'right 0.2s ease',
            }}
          >
            {isRightPanelVisible ? (
              <ChevronRightIcon fontSize="small" />
            ) : (
              <ChevronLeftIcon fontSize="small" />
            )}
          </button>
        </div>
      </div>
    </div>
  );
}

export default App;
