// src/components/SettingsPopup.js

import React, { useState, useEffect } from 'react';
import './SettingsPopup.css';

const SettingsPopup = ({ onClose, isOpen }) => {
  const [activeTab, setActiveTab] = useState('grid');
  const [gridSize, setGridSize] = useState(10);
  const [unit, setUnit] = useState('nm');
  const [gridColor, setGridColor] = useState('#444444');
  const [showGrid, setShowGrid] = useState(true);
  const [gridType, setGridType] = useState('line');


  useEffect(() => {
    if (window.canvasEngine) {
      setGridSize(window.canvasEngine.gridSpacing || 10);
      setGridColor(window.canvasEngine.gridColor || '#444444');
      setShowGrid(window.canvasEngine.showGrid ?? true);
    }
  }, [isOpen]);

  const handleGridChange = (e) => {
    const value = parseInt(e.target.value);
    setGridSize(value);
    window.canvasEngine?.setGridSpacing(value);
  };

  const handleGridTypeChange = (e) => {
    const newType = e.target.value;
    setGridType(newType);
    if (window.canvasEngine) {
      window.canvasEngine.gridType = newType;
      window.canvasEngine.draw();
    }
  };
  

  const handleGridColorChange = (e) => {
    const color = e.target.value;
    setGridColor(color);
    if (window.canvasEngine) {
      window.canvasEngine.gridColor = color;
      window.canvasEngine.draw();
    }
  };

  const handleGridToggle = (e) => {
    const checked = e.target.checked;
    setShowGrid(checked);
    if (window.canvasEngine) {
      window.canvasEngine.showGrid = checked;
      window.canvasEngine.draw();
    }
  };

  const getSliderProps = () => {
    if (unit === 'nm') return { min: 10, max: 2000, step: 10 };
    if (unit === 'um') return { min: 10000, max: 200000, step: 1000 };
    if (unit === 'px') return { min: 1, max: 200, step: 1 };
  };

  const getDisplayValue = () => {
    if (unit === 'nm') return `${(gridSize / 10).toFixed(1)} nm`;
    if (unit === 'um') return `${(gridSize / 10000).toFixed(2)} µm`;
    if (unit === 'px') return `${gridSize}px`;
  };

  const handleUnitChange = (e) => {
    const newUnit = e.target.value;
    setUnit(newUnit);
  };

  if (!isOpen) return null;

  const sliderProps = getSliderProps();

  return (
    <div className="settings-popup">
      <div className="settings-header">
        <h3>Drawing Grid Settings</h3> {/* ✅ Heading updated */}
        <button className="close-btn" onClick={onClose}>×</button>
      </div>

      <div className="settings-tabs">
        <button className={activeTab === 'grid' ? 'active' : ''} onClick={() => setActiveTab('grid')}>Drawing Grid</button>
        <button disabled>Technology Grid</button> {/* ✅ Future placeholder */}
      </div>

      <div className="settings-content">
  {activeTab === 'grid' && (
    <div className="grid-settings">

<div className="setting-row">
  <label>Show Grid</label>
  <div className="toggle-gridtype">
    <div className="radio-group">
      <label>
        <input
          type="radio"
          name="gridType"
          value="line"
          checked={gridType === 'line'}
          onChange={handleGridTypeChange}
        />
        Line
      </label>
      <label>
        <input
          type="radio"
          name="gridType"
          value="dots"
          checked={gridType === 'dots'}
          onChange={handleGridTypeChange}
        />
        Dots
      </label>
    </div>

    <label className="switch">
      <input type="checkbox" checked={showGrid} onChange={handleGridToggle} />
      <span className="slider round"></span>
    </label>
  </div>
</div>


      <div className="setting-row">
        <label>Grid Spacing</label>
        <div className="slider-row">
          <input
            type="range"
            id="gridSize"
            value={gridSize}
            onChange={handleGridChange}
            {...sliderProps}
          />
          <span className="slider-value">{getDisplayValue()}</span>
        </div>
      </div>

      <div className="setting-row">
        <label>Units</label>
        <select id="unit-select" value={unit} onChange={handleUnitChange}>
          <option value="px">Pixels</option>
          <option value="nm">Nanometers</option>
          <option value="um">Micrometers</option>
        </select>
      </div>

      <div className="setting-row">
        <label>Grid Color</label>
        <input type="color" id="gridColor" value={gridColor} onChange={handleGridColorChange} />
      </div>

    </div>
  )}
</div>

    </div>
  );
};

export default SettingsPopup;
