import React from 'react';
import './RulerControls.css';
import RestartAltIcon from '@mui/icons-material/RestartAlt';

const RulerControls = ({
  unit,
  setUnit,
  drawMode,
  setDrawMode,
  lineMode,
  setLineMode,
  tickCount,
  setTickCount,
  onReset,
  rulerColor,
  setRulerColor,
  onGrid,
  setOnGrid
}) => {
  return (
    <div className="ruler-controls">
      <select value={unit} onChange={(e) => setUnit(e.target.value)}>
        <option value="1">Px</option>
        <option value="264.583">Micrometers</option>
        <option value="264583">Nanometers</option>
      </select>

      <select value={drawMode} onChange={(e) => setDrawMode(e.target.value)}>
        <option value="two">Two Points</option>
        <option value="multi">Multiple Points</option>
      </select>

      <select value={lineMode} onChange={(e) => setLineMode(e.target.value)}>
        <option value="free">Free</option>
        <option value="diagonal">Diagonal</option>
        <option value="orthogonal">Orthogonal</option>
        <option value="5">5 Degrees</option>
        <option value="10">10 Degrees</option>
        <option value="15">15 Degrees</option>
      </select>

      <select value={tickCount} onChange={(e) => setTickCount(parseInt(e.target.value))}>
        {[...Array(10).keys()].map(i => (
          <option key={i+1} value={i+1}>{i+1} Values</option>
        ))}
      </select>

      <label>
        <select value={onGrid} onChange={(e) => setOnGrid(e.target.value === 'yes')}>
          <option value="yes">Yes</option>
          <option value="no">No</option>
        </select>
      </label>

      <label>
        <input
          type="color"
          value={rulerColor}
          onChange={(e) => setRulerColor(e.target.value)}
          style={{ marginLeft: '2px' }}
        />
      </label>

      <RestartAltIcon
        className="reset-icon"
        onClick={onReset}
        style={{ cursor: 'pointer', color: '#fff' }}
        titleAccess="Reset Ruler"
      />
    </div>
  );
};

export default RulerControls;
