import React, { useState } from 'react';
import DrcViewer from '../DRC/DrcViewer';
import './leftbar.css';
import CircleIcon from '@mui/icons-material/Circle';
import SquareIcon from '@mui/icons-material/Square';
import ChangeHistoryIcon from '@mui/icons-material/ChangeHistory';
import DeleteIcon from '@mui/icons-material/Delete';
import SettingsIcon from '@mui/icons-material/Settings';
import SchemaIcon from '@mui/icons-material/Schema';
import LocalOfferIcon from '@mui/icons-material/LocalOffer';
import Inventory2Icon from '@mui/icons-material/Inventory2';
import StraightenIcon from '@mui/icons-material/Straighten';
import CheckCircleIcon  from '@mui/icons-material/CheckCircle';
import SettingsPopup from './SettingsPopup';
import ToolManager from '../libraries/ToolManager';

const LeftBar = ({ onIconClick }) => {
  const [isSettingsOpen, setIsSettingsOpen] = useState(false);
   const [isDrcViewerOpen, setIsDrcViewerOpen] = useState(false);
  const [isShapesDropdownOpen, setIsShapesDropdownOpen] = useState(false); // State to toggle dropdown visibility
  const [selectedShape, setSelectedShape] = useState('rectangle'); // State to track selected shape

  const shapes = [
    { id: "rectangle", icon: <SquareIcon fontSize="small" />, label: 'Rectangle' },
    { id: "circle", icon: <CircleIcon fontSize="small" />, label: 'Circle' },
    { id: "triangle", icon: <ChangeHistoryIcon fontSize="small" />, label: 'Triangle' },
  ];

  const handletag = (tagId) => {
    ToolManager.setTool(tagId);
    window.dispatchEvent(new CustomEvent("tool-changed", { detail: tagId }));
  };
  const handleSelectShape = (shapeId) => {
      ToolManager.setTool(shapeId);
    setSelectedShape(shapeId); // Update the selected shape
    setIsShapesDropdownOpen(false); // Close the dropdown
    window.dispatchEvent(new CustomEvent("tool-changed", { detail: shapeId })); // shiv code
    };
    const toggleDrcViewer = () => {
    setIsDrcViewerOpen(!isDrcViewerOpen);
  };


  return (
    <>
      <div className="left-bar">
        <div className="icon-container">
          {/* Shapes Dropdown */}
          <div className="dropdown-container">
            <div
              className="dropdown-trigger"
              onClick={() => setIsShapesDropdownOpen(!isShapesDropdownOpen)}
            >
              {shapes.find((shape) => shape.id === selectedShape)?.icon} {/* Display selected icon */}
            </div>
            {isShapesDropdownOpen && (
              <div className="dropdown-menu">
                {shapes.map((shape) => (
                  <div
                    key={shape.id}
                    className={`dropdown-item ${
                      selectedShape === shape.id ? 'selected' : ''
                    }`}
                    onClick={() => handleSelectShape(shape.id)}
                  >
                    {shape.icon} {shape.label}
                  </div>
                ))}
              </div>
            )}
          </div>

          <LocalOfferIcon
            fontSize="small"
            className="icon"
            titleAccess="Label Tool"
            onClick={() => handletag("label")}
          />
          <StraightenIcon fontSize="small" className="icon" titleAccess="Ruler" onClick={()=>handletag("ruler")} />
          <Inventory2Icon fontSize="small" className="icon" />
          <SchemaIcon fontSize="small" className="icon" />
          <DeleteIcon fontSize="small" className="icon" />
          {/* <CheckCircleIcon fontSize="small" titleAccess='Drc viewer' className="icon" onClick={toggleDrcViewer} /> */}
        </div>
        <div className="bottom-icon">
            <SettingsIcon fontSize="small" titleAccess="Settings" className="icon" onClick={() => setIsSettingsOpen(true)}/>
      </div>
      </div>
      {/* {isDrcViewerOpen && <DrcViewer onClose={toggleDrcViewer} />} */}
      <SettingsPopup isOpen={isSettingsOpen} onClose={() => setIsSettingsOpen(false)} />
    </>
  );
};

export default LeftBar;