// ✅ In-memory virtual layout manager for frontend
import UploadedLayoutManager from "./UploadedLayoutManager";

const layoutEngine = (() => {
  let createdCells = [];     // ✅ manually created cells
  let uploadedCells = [];    // ✅ uploaded from GDS
  let activeCellStack = [];

  const findCell = (name) => createdCells.find(cell => cell.name === name);
  const getCellByName = (name) => createdCells.find(cell => cell.name === name);
  const getCells = () => createdCells;
  const getUploadedCells = () => uploadedCells;
  const setUploadedCells = (newCells) => { uploadedCells = newCells; };
  const generateId = () => {
  return Math.random().toString(16).slice(2) + Date.now().toString(16);
};



  const reset = () => {
    createdCells = [];
    uploadedCells = [];
    activeCellStack = [];
  };

  const addCell = (name) => {
    if (!findCell(name)) {
      createdCells.push({ name, elements: [], instances: [] });
      return true;
    }
    return false;
  };

  const getCell = (name) => {
    return uploadedCells.find(c => c.name === name) || createdCells.find(c => c.name === name);
  };
  

  function createsCycle(currentCell, targetCell) {
    if (currentCell === targetCell) return true;
    const target = getCellByName(targetCell);
    if (!target) return false;
    for (let inst of target.instances || []) {
      if (createsCycle(currentCell, inst.ref)) return true;
    }
    return false;
  }

  const addRectangle = (cellName, x1, y1, x2, y2, layer = 1) => {
    const cell = findCell(cellName);
    if (cell) {
      cell.elements.push({ id: generateId(), type: 'rectangle', x1, y1, x2, y2, layer });
    }
  };

  const addText = (cellName, text, x, y, layer = 1) => {
    const cell = findCell(cellName);
    if (cell) {
      cell.elements.push({ id: generateId(), type: 'text', text, x, y, layer });
    }
  };

  const getActiveCellName = () => {
    return activeCellStack.length > 0 ? activeCellStack[activeCellStack.length - 1] : null;
  };

  const deleteElement = (cellName, elementId) => {
    const cell = findCell(cellName);
    if (cell) {
      cell.elements = cell.elements.filter(el => el.id !== elementId);
    }
  };

  const addInstance = (parentCell, refCellName, x, y, rotation = 0) => {
    const cell = findCell(parentCell);
    console.log("💥 Adding instance to cell:", cell);

    if (createsCycle(parentCell, refCellName)) {
      console.warn(`❌ Cannot insert ${refCellName} into ${parentCell} — would cause circular hierarchy`);
      return;
    }

    if (cell) {
      const inst = {
        id: generateId(),
        ref: refCellName,
        x,            
        y,
        rotation,
        scale: 1
      };
      cell.instances.push(inst);
      console.log("✅ Added instance:", inst);
    } else {
      console.error("❌ Cell not found:", parentCell);
    }
  };

  const deleteInstance = (cellName, instanceId) => {
    const cell = findCell(cellName);
    if (cell) {
      cell.instances = cell.instances.filter(inst => inst.id !== instanceId);
    }
  };

  const editElement = (cellName, elementId, updates) => {
    const cell = findCell(cellName);
    if (cell) {
      const el = cell.elements.find(e => e.id === elementId);
      if (el) Object.assign(el, updates);
    }
  };

  const editInstance = (cellName, instanceId, updates) => {
    const cell = findCell(cellName);
    if (cell) {
      const inst = cell.instances.find(i => i.id === instanceId);
      if (inst) Object.assign(inst, updates);
    }
  };

  const getAllCellNames = () => {
    return createdCells.map(cell => cell.name);
  };

  const loadFromGDS = (gdsData) => {
    if (!gdsData || !Array.isArray(gdsData)) return;
  
    const userSize = gdsData?.[0]?.user_unit || 0.001; // ⛳ fallback if not present
    window.canvasEngine.setUseUploadedCells(true);
    UploadedLayoutManager.load(gdsData);
  
    const nameToCellMap = {};
    gdsData.forEach(cell => nameToCellMap[cell.name] = cell);
  
    const resultCells = [];
    const visited = new Set();
  
    const applyInstanceOffsets = (cellName, accumulatedTransform = { x: 0, y: 0, rotation: 0 }) => {
      if (visited.has(cellName)) return;
      visited.add(cellName);
  
      const cell = nameToCellMap[cellName];
      if (!cell) return;
      console.log("cell name",cell)

  
      const newElements = (cell.elements || []).map(el => {
        console.log(el.x, el.y)
        if (el.type === 'rectangle') {
          return {
            id: generateId(),
            type: 'rectangle',
            x: (el.x ) / userSize,
            y: -(el.y ) / userSize,
            width: el.width / userSize,
            height: -el.height / userSize,
            layer: `${el.layer}`,
            datatype: el.datatype ?? 0
          };
        } else if (el.type === 'Label') {
          return {
            id: generateId(),
            type: 'Label',
            coordinates: [[
              (el.coordinates[0][0] ) / userSize,
              -(el.coordinates[0][1] ) / userSize 
            ]],
            name: el.name,
            layer: `${el.layer_number}`,
            datatype: el.datatype_number ?? 0
          };
        }
        return el;
      });
  
      const newInstances = (cell.instances || []).map(inst => {
        const instX = inst.x ;
        const instY = inst.y ;
  
        const newInst = {
          id: generateId(),
          ref: inst.name,
          x: inst.x  / userSize,
          y: -inst.y / userSize,
          rotation: inst.rotation || 0,
          scale: inst.scale || 1
        };
  
        // recursively apply offsets to child cell
        applyInstanceOffsets(inst.name, { x: instX, y: instY, rotation: 0 });
  
        return newInst;
      });
  
      resultCells.push({ name: cell.name, elements: newElements, instances: newInstances });
    };
  
    const topCells = gdsData.filter(c =>
      !gdsData.some(parent => (parent.instances || []).some(inst => inst.name === c.name))
    );
  
    topCells.forEach(topCell => applyInstanceOffsets(topCell.name, { x: 0, y: 0, rotation: 0 }));
  
    layoutEngine.setUploadedCells(resultCells);
    console.log("📥 Final merged cells loaded:", resultCells);
  };
  
  function getUploadedCell(cellName) {
    return uploadedCells.find(cell => cell.name === cellName);
  }
  
  

  const showAsTop = (cellName) => {
    if (!findCell(cellName)) {
      createdCells.push({ name: cellName, elements: [], instances: [] });
      console.warn(`📦 Dynamically added cell for: ${cellName}`);
    }
    activeCellStack.push(cellName);
    console.log("🔁 Switched to cell:", cellName);
  };

  const exitShowAsTop = () => {
    if (activeCellStack.length > 1) {
      const popped = activeCellStack.pop();
      console.log("🔙 Exited cell:", popped, "➡️ Now in:", activeCellStack.at(-1));
    } else {
      console.warn("⚠️ Already at TOP, can't go back further.");
    }
  };

  return {
    getCells,
    getUploadedCells,
    setUploadedCells,
    reset,
    addCell,
    addRectangle,
    addText,
    addInstance,
    deleteElement,
    deleteInstance,
    editElement,
    editInstance,
    getCellByName,
    getActiveCellName,
    showAsTop,
    exitShowAsTop,
    getAllCellNames,
    loadFromGDS,
    getCell,
    getUploadedCell,
  };
})();

if (typeof window !== 'undefined') {
  window.layoutEngine = layoutEngine;
}

export default layoutEngine;
