import layoutEngine from '../libraries/layoutEngine';
import ProjectManager from '../canvas/ProjectManager';

export default class InstancePlacementHandler {
  constructor(canvasEngine) {
    this.engine = canvasEngine;
  }

  updatePreview(e) {
    const ref = ProjectManager.getSelectedInstanceCell();
    if (!ref) return;
  
    const cell = layoutEngine.getCellByName(ref);
    if (!cell) {
      console.warn("❌ Cannot find cell for ref:", ref);
      return; // 🔥 Avoid crash
    }
  
    const { offsetX, offsetY, scale } = this.engine;
    const snappedX = this.engine.snapToGrid((e.offsetX - offsetX) / scale);
    const snappedY = this.engine.snapToGrid((e.offsetY - offsetY) / scale);
  
    this.engine.previewInstance = { ref, x: snappedX, y: snappedY };
    this.engine.draw();
  }
  
  finalizePlacement() {
  if (!this.engine.previewInstance) return;

  const { ref, x, y } = this.engine.previewInstance;
  const cellName = ProjectManager.getSelectedCell();
  const currentCell = layoutEngine.getCellByName(cellName);

  const cell = layoutEngine.getCellByName(ref);
  const { offsetX, offsetY } = this.engine.getChildBoundingBoxCenterOffset(cell);

  // 🔄 Recalculate and snap again (to be 100% sure it's aligned)
  const snappedCanvasX = this.engine.snapToGrid(x + offsetX);
  const snappedCanvasY = this.engine.snapToGrid(y + offsetY);
  const finalX = snappedCanvasX - offsetX;
  const finalY = snappedCanvasY - offsetY;

  // ✅ Updated duplicate check based on snapped position
  if (currentCell?.instances?.some(inst => inst.ref === ref && inst.x === finalX && inst.y === finalY)) {
    console.warn("🛑 Skipping duplicate instance placement");
    this.engine.previewInstance = null;
    this.engine.draw();
    return;
  }

  // Add the instance to the current cell with the adjusted final positions
  layoutEngine.addInstance(cellName, ref, finalX, finalY);

  this.engine.previewInstance = null;
  this.engine.draw();
}

  
  
  
}
