import React, { useEffect, useRef, useState } from 'react';
import selectionLibrary from '../libraries/SelectionLibrary';
import layerLibrary from '../canvas/LayerLibrary';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import { hexToRgba } from '../libraries/RectangleLibrary';

const PropertyPanel = () => {
  const [selected, setSelected] = useState([]);
  const [selectedType, setSelectedType] = useState(null);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);
  const scrollRef = useRef(null);

  useEffect(() => {
    selectionLibrary.onChange((sel) => {
      // console.log('Selection changed:', sel);
      setSelected(sel);
      setCurrentIndex(0);
      if (sel.length > 0) {
        setSelectedType(sel[0].type);
      }
    });

    const currentSelection = selectionLibrary.getSelection();
    setSelected(currentSelection);
    if (currentSelection.length > 0) {
      setSelectedType(currentSelection[0].type);
    }
  }, []);

  useEffect(() => {
    const activeButton = scrollRef.current?.querySelector('.active-tab');
    if (activeButton && scrollRef.current) {
      const offsetLeft = activeButton.offsetLeft;
      const scrollWidth = scrollRef.current.offsetWidth;
      scrollRef.current.scrollTo({
        left: offsetLeft - scrollWidth / 2 + activeButton.offsetWidth / 2,
        behavior: 'smooth',
      });
    }
  }, [selectedType]);

  const getGroupedByType = () => {
    return selected.reduce((acc, obj) => {
      if (!acc[obj.type]) acc[obj.type] = [];
      acc[obj.type].push(obj);
      return acc;
    }, {});
  };

  if (!selected || selected.length === 0) {
    return <div style={{ color: '#888' }}>No object selected</div>;
  }

  const grouped = getGroupedByType();
  const typeOptions = Object.keys(grouped);
  const objectsOfSelectedType = grouped[selectedType] || [];

  const currentObj = objectsOfSelectedType[currentIndex];
  const indexInSelected = selected.findIndex((s) => s.id === currentObj.id);
  const allLayers = layerLibrary.getAllLayers();
  const layerOptions = Object.values(allLayers);
  let currentLayer = layerOptions.find((l) => l.id === currentObj.ref?.layerId);
  // Fallback: if not found, get by string match (layer_number:datatype_number)
    // if (!currentLayer && currentObj.ref?.layerId) {
    //   currentLayer = layerLibrary.getLayerById(currentObj.ref.layerId);
    // }
    if (!currentLayer && currentObj.ref?.layerId) {
  const [layerNum, dataTypeNum] = currentObj.ref.layerId.split(":").map(Number);
  currentLayer = layerLibrary.getLayerByNumber(layerNum, dataTypeNum);
}


  const handleChange = (index, property, value) => {
    if (window.canvasEngine) {
      window.canvasEngine.updateSelectedPropertyById(currentObj.id, currentObj.type, property, value);
      // const updated = [...selected];
      // updated[index] = { ...currentObj, [property]: value };

      // shiv code
      const updated = [...selected];
      updated[index] = {...currentObj,ref: {...currentObj.ref,[property]: value}};
      // end shiv code

      setSelected(updated);
    }
  };

 // In PropertyPanel.jsx - update the handleLayerSelect function
const handleLayerSelect = (layer) => {
  if (!layer) return;
  
  const newLayerId = `${layer.layer_number}:${layer.datatype_number}`;
  
  if (window.canvasEngine?.updateSelectedLayer) {
    window.canvasEngine.updateSelectedLayer(newLayerId);
  }

  // Update local state to reflect changes
  const updatedSelected = selected.map(obj => {
    if (obj.type === 'rectangle') {
      return {
        ...obj,
        ref: {
          ...obj.ref,
          layerId: newLayerId,
          fill: hexToRgba(layer.color, 0.4)
        }
      };
    } else if (obj.type === 'label') {
      return {
        ...obj,
        ref: {
          ...obj.ref,
          layerId: newLayerId,
          color: layer.color
        }
      };
    }
    return obj;
  });

  selectionLibrary.setSelection(updatedSelected);
  setSelected(updatedSelected);
  setIsDropdownOpen(false);
};
  // Update the handleLayerSelect function
//     const handleLayerSelect = (layer) => {
//   if (!layer) return;
  
//   // Create the layer ID using the same format as LayerLibrary._getKey()
//   const layerId = `${layer.layer_number}:${layer.datatype_number}`;
//   const updatedObject = { 
//     ...currentObj, 
//     ref: {
//       ...currentObj.ref,
//       layerId: layerId,
//       // Update the color to match the new layer's color
//       fill: layer.color,  // For rectangles
//       color: layer.color  // For labels
//     }
//   };

//   // Update in canvas engine
//   if (window.canvasEngine?.updateSelectedPropertyById) {
//     window.canvasEngine.updateSelectedPropertyById(
//       updatedObject.id,
//       updatedObject.type,
//       'layerId',
//       layerId
//     );
//     // Also update the color property
//     window.canvasEngine.updateSelectedPropertyById(
//       updatedObject.id,
//       updatedObject.type,
//       'fill',
//       layer.color
//     );
//   }

//   // Replace in selection
//   const updatedSelected = [...selected];
//   updatedSelected[indexInSelected] = updatedObject;

//   // Update global and local state
//   selectionLibrary.setSelection(updatedSelected);
//   setSelected(updatedSelected);
  
//   // Force redraw and close dropdown
//   window.canvasEngine?.draw?.();
//   setIsDropdownOpen(false);
// };





  return (
    <div style={{ padding: '6px', color: '#ddd', fontSize: '14px' }}>
      <div style={scrollContainerStyle} ref={scrollRef}>
        {typeOptions.map((type) => (
          <button
            key={type}
            onClick={() => {
              setSelectedType(type);
              setCurrentIndex(0);
            }}
            className={selectedType === type ? 'active-tab' : ''}
            style={{
              ...buttonStyle,
              ...(selectedType === type ? activeButtonStyle : {}),
            }}
          >
            {type}
          </button>
        ))}
      </div>

      <h3 style={{ ...headerStyle, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <span>
              {selectedType ? selectedType.charAt(0).toUpperCase() + selectedType.slice(1) : ''} ({currentIndex + 1}/{objectsOfSelectedType.length})
        </span>
        <div style={{ display: 'flex', gap: '2px' }}>
    <button
      disabled={currentIndex === 0}
      onClick={() => setCurrentIndex(currentIndex - 1)}
      style={{
        // backgroundColor: 'black',
        color: 'black',
        height:'25px',
        width:'25px',
        border: '1px solid #555',
        borderRadius: '4px',
        padding: '0px',
        cursor: currentIndex === 0 ? 'hidden' : 'pointer',
      }}
    >
      <ChevronLeftIcon />
    </button>
    <button
      disabled={currentIndex === objectsOfSelectedType.length - 1}
      onClick={() => setCurrentIndex(currentIndex + 1)}
      style={{
        // backgroundColor: '#333',
        color: 'black',
        border: '1px solid #555',
        height:'25px',
        width:'25px',
        borderRadius: '4px',
        padding: '0px',
        cursor: currentIndex === objectsOfSelectedType.length - 1 ? 'hidden' : 'pointer',
      }}
    >
      <ChevronRightIcon />
    </button>
        </div>
      </h3>

      {/* Toggle Switch */}
    <div style={row}>
      <label>Common property for all:</label>
        <label style={{ position: 'relative', display: 'inline-block', width: '50px', height: '24px' }}>
      <input
      type="checkbox"
      checked={currentObj.ref?.isEnabled}
      onChange={() => handleChange(indexInSelected, 'isEnabled', !currentObj.ref?.isEnabled)}
      style={{
        opacity: 0,
        width: 0,
        height: 0,
      }}
      />
      <span
      style={{
        position: 'absolute',
        cursor: 'pointer',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: currentObj.ref?.isEnabled ? '#4CAF50' : '#ccc', // Green for ON, Gray for OFF
        borderRadius: '24px',
        transition: '0.4s',
      }}
      ></span>
      <span
      style={{
        position: 'absolute',
        content: '""',
        height: '18px',
        width: '18px',
        left: currentObj.ref?.isEnabled ? '26px' : '4px', // Move the toggle
        bottom: '3px',
        backgroundColor: '#fff',
        borderRadius: '50%',
        transition: '0.4s',
      }}
      ></span>
    </label>
    </div>

      {/* Custom Dropdown */}
      <div style={row}>
        <label style={{ width: '80px' }}>Layer:</label>
        <div style={{
      position: 'relative', // Keep it relative for dropdown positioning
      flex: 1,
      height: '20px', // Match the height of other input fields
      width: '150px', // Ensure consistent width
      padding: '5px',
      border: '1px solid #555',
      borderRadius: '4px',
      backgroundColor: '#333',
      color: '#ddd',
    }}>
          <div
            onClick={() => setIsDropdownOpen(!isDropdownOpen)}
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'space-between',
              height: '100%', // Ensure it matches the height of the container
              cursor: 'pointer',
              // width: '150%',
              padding: '0 8px', // Add padding for better alignment
            }}
          >
            <div
              style={{
                width: '12px',
                height: '12px',
                backgroundColor: currentLayer?.color || '#000',
                marginRight: '8px',
                border: '1px solid #555',
              }}
            />
            <span title={currentLayer?.id || currentObj.layerId || 'Unknown'}>
              {currentLayer
              ? `${currentLayer.layer_name} (L${currentLayer.layer_number}/DT${currentLayer.datatype_number})`
              : `Unknown Layer (${currentObj.layerId})`}
            </span>
          </div>
          {isDropdownOpen && (
            <div
              style={{
                position: 'absolute',
                top: '100%',
                left: '0px',
                right: 0,
                width: '120%',
                backgroundColor: '#222',
                border: '1px solid #555',
                zIndex: 10,
                maxHeight: '300px',
                overflowY: 'auto',
              }}
            >
              {layerOptions.map((layer) => (
                <div
                  key={layer.id}
                  onClick={() => handleLayerSelect(layer)}
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    padding: '6px 10px',
                    width: '130%',
                    cursor: 'pointer',
                    backgroundColor: currentLayer?.id === layer.id ? '#444' : 'transparent',
                    color: '#ddd',
                  }}
                >
                  <div
                    style={{
                      width: '12px',
                      height: '12px',
                      backgroundColor: layer.color,
                      marginRight: '8px',
                      border: '1px solid #555',
                    }}
                  />
                  {layer.layer_name} ({layer.layer_number}/{layer.datatype_number})
                </div>
              ))}
            </div>
          )}
        </div>
      </div>

      {/* custom code for  */}
      {selectedType === 'rectangle' && (
        <>
          <div style={row}>
        <label>X:</label>
        <input type="number" style={inputb} value={currentObj.ref?.x} onChange={(e) => handleChange(indexInSelected, 'x', parseFloat(e.target.value))} />
      </div>

      <div style={row}>
        <label>Y:</label>
        <input type="number" style={inputb} value={currentObj.ref?.y*(-1) - currentObj.ref?.height} onChange={(e) => handleChange(indexInSelected, 'y', (-1)*parseFloat(e.target.value))} />
      </div>

        <div style={row}>
          <label>Width:</label>
          <input type="number" style={inputb} value={currentObj.ref?.width} onChange={(e) => handleChange(indexInSelected, 'width', parseFloat(e.target.value))} />
        </div>

        <div style={row}>
          <label>Height:</label>
          <input type="number" style={inputb} value={currentObj.ref?.height} onChange={(e) => handleChange(indexInSelected, 'height', parseFloat(e.target.value))} />
        </div>

        <div style={row}>
            <label>Rotation:</label>
            <select
                style={{ background: 'transparent', color: 'white', width: '148px', height: '25px', borderRadius: '4px' }}
                value={currentObj.ref?.rotation || 0}
                onChange={(e) => handleChange(indexInSelected, 'rotation', parseInt(e.target.value))}
            >
            <option style={{ background: 'black' }} value={0}>R0</option>
            <option style={{ background: 'black' }} value={90}>R90</option>
            <option style={{ background: 'black' }} value={180}>R180</option>
            <option style={{ background: 'black' }} value={270}>R270</option>
            </select>
        </div>
        </>
      )}
      {selectedType === 'label' && (
        <>
          <div style={row}>
        <label>X:</label>
        <input type="number" style={inputb} value={currentObj.ref?.x} onChange={(e) => handleChange(indexInSelected, 'x', parseFloat(e.target.value))} />
      </div>

      <div style={row}>
        <label>Y:</label>
        <input type="number" style={inputb} value={currentObj.ref?.y*(-1)} onChange={(e) => handleChange(indexInSelected, 'y', (-1)*parseFloat(e.target.value))} />
      </div>

        <div style={row}>
            <label>Font Size:</label>
            <input
              style={inputb}
              type="number"
              value={currentObj.ref?.fontSize || ''}
              onChange={(e) => handleChange(indexInSelected, 'fontSize', parseFloat(e.target.value))}
            />
        </div>
          <div style={row}>
            <label>Text:</label>
            <input
            style={inputb}
              type="text"
              value={currentObj.ref?.text || ''}
              onChange={(e) => handleChange(indexInSelected, 'text', e.target.value)}
            />
          </div>
          <div style={row}>
            <label>FontType:</label>
            <select
              style={{background:'transparent',color:'white', width:'148px',height:'25px',borderRadius:'4px'}}
              value={currentObj.ref?.fontType || 'monospace'}
                onChange={(e) => handleChange(indexInSelected, 'fontType', e.target.value)}
            >
              <option style={{background:'black'}} value="monospace">Monospace</option>
              <option style={{background:'black'}} value="Arial">Arial</option>
              <option style={{background:'black'}} value="Verdana">Verdana</option>
              <option style={{background:'black'}} value="Times New Roman">Times New Roman</option>
              <option style={{background:'black'}} value="Courier New">Courier New</option>
            </select>
          </div>
          <div style={row}>
            <label>Rotation:</label>
            <input
              style={inputb}
              type="number"
              value={currentObj.ref?.rotation || ''}
              onChange={(e) => handleChange(indexInSelected, 'rotation', parseFloat(e.target.value))}
            />
          </div>
          <div style={row}>
            <label>Justification:</label>
            <select
              style={{background:'transparent',color:'white', width:'148px',height:'25px',borderRadius:'4px'}}
              value={currentObj.ref?.justification || 'left'}
                onChange={(e) => handleChange(indexInSelected, 'justification', e.target.value)}
            >
              <option style={{background:'black'}} value="centerleft">Center Left</option>
              <option style={{background:'black'}} value="centerright">Center Right</option>
              <option style={{background:'black'}} value="center">Center</option>
              <option style={{background:'black'}} value="upperleft">upper Left</option>
              <option style={{background:'black'}} value="upperright">upper Right</option>
              <option style={{background:'black'}} value="lowerleft">lower Left</option>
              <option style={{background:'black'}} value="lowerright">lower Right</option>
            </select>
          </div>
          <div style={row}>
              <label>Fill:</label>
              <input type="color" value={currentObj.ref?.fill || currentObj.color || '#000000'} onChange={(e) => handleChange(indexInSelected, 'fill', e.target.value)} />
          </div>

          <div style={row}>
              <label>Stroke:</label>
              <input type="color" value={currentObj.ref?.stroke || '#000000'} onChange={(e) => handleChange(indexInSelected, 'stroke', e.target.value)} />
          </div>
        </>
      )}
  
      {selectedType === 'instance' && (
        <>
          <div style={row}>
            <label>Instance Name:</label>
            <input
              type="text"
              value={currentObj.instanceName || ''}
              onChange={(e) => handleChange(indexInSelected, 'instanceName', e.target.value)}
            />
          </div>
        </>
      )}
    </div>
  );
};

// Styling
const row = {
  marginBottom: '8px',
  fontSize: '14px',
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
};

const inputb = {
  height: '15px',
  width: '137px',
  padding: '5px',
  border: '1px solid #555',
  borderRadius: '4px',
  backgroundColor: '#333',
  color: '#ddd',
};

const iconStyle = {
  color: '#aaa',
  fontSize: '20px',
  cursor: 'pointer', // Add pointer cursor for interactivity
};
const scrollContainerStyle = {
  display: 'flex',
  overflowX: 'auto',
  gap: '0px',
  paddingBottom: '0px',
  marginBottom: '15px',
  whiteSpace: 'nowrap',
  scrollbarWidth: 'none',
  msOverflowStyle: 'none',
};

const buttonStyle = {
  padding: '6px 10px',
  backgroundColor: '#333',
  color: '#ddd',
  border: '1px solid #555',
  borderRadius: '0px',
  whiteSpace: 'nowrap',
  cursor: 'pointer',
  flexShrink: 0,
};

const activeButtonStyle = {
  backgroundColor: '#555',
  color: '#fff',
  border: '1px solid #aaa',
};

const headerStyle = {
  fontSize: '16px',
  marginBottom: '10px',
  borderBottom: '1px solid #444',
  paddingBottom: '5px',
};

export default PropertyPanel;