import React, { useEffect, useState } from 'react';
import layoutEngine from '../libraries/layoutEngine';
import ProjectManager from '../canvas/ProjectManager';
import { getInstanceCounts } from '../libraries/HierarchyManager';


const buildHierarchyTree = () => {
  const created = layoutEngine.getCells();
  const uploaded = layoutEngine.getUploadedCells();
  const cells = [...created, ...uploaded]; // 🧠 merge both sources

  const cellMap = new Map();
  cells.forEach(cell => cellMap.set(cell.name, { name: cell.name, children: [] }));

  const counts = getInstanceCounts();
  const referenced = new Set();

  const buildChildrenRecursive = (parentNode, visited = new Set()) => {
    const cell = cells.find(c => c.name === parentNode.name);
    if (!cell || visited.has(parentNode.name)) return;

    visited.add(parentNode.name);

    for (const instance of cell.instances || []) {
      const childNode = cellMap.get(instance.ref);
      if (childNode) {
        referenced.add(instance.ref);
        parentNode.children.push(childNode);
        buildChildrenRecursive(childNode, visited);
      }
    }
  };

  const roots = [];
  for (const cell of cells) {
    if (!cells.some(c => (c.instances || []).some(i => i.ref === cell.name))) {
      const rootNode = cellMap.get(cell.name);
      buildChildrenRecursive(rootNode);
      roots.push(rootNode);
    }
  }

  return { roots, counts };
};




const TreeNode = ({ node, activeCell, onSelect, counts }) => {
  const [expanded, setExpanded] = useState(true);

  const totalRefs = node.children.map(c => c.name);
  const countDisplay = totalRefs.length === 0 ? '' : ` (x${totalRefs.length})`;

  return (
    <div style={{ marginLeft: 15 }}>
      <div
        style={{
          backgroundColor: node.name === activeCell ? '#444' : 'transparent',
          borderRadius: '6px',
          padding: '4px 6px',
          cursor: 'pointer',
          color: '#fff'
        }}
        onClick={() => onSelect(node.name)}
      >
        {node.children.length > 0 && (
          <span onClick={(e) => { e.stopPropagation(); setExpanded(!expanded); }}>
            {expanded ? '📂' : '📁'}
          </span>
        )}{' '}
        📄 {node.name}
        {counts[node.name] ? ` (x${counts[node.name]})` : ''}
      </div>
      {expanded && node.children.map(child => {
  const countKey = `${node.name}->${child.name}`;
  const count = counts[countKey] || 1;
  return (
    <TreeNode
      key={`${node.name}-${child.name}`}
      node={child}
      activeCell={activeCell}
      onSelect={onSelect}
      counts={counts}
    />
  );
})}

    </div>
  );
};


const HierarchyPanel = () => {
  const [treeRoots, setTreeRoots] = useState([]);
  const [activeCell, setActiveCell] = useState(ProjectManager.getSelectedCell());
  const [counts, setCounts] = useState({});


  
// ❌ NO COUNTS HERE!


// useEffect(() => {
//   const { roots, counts } = buildHierarchyTree(); // ✅ destructure correctly
//   setTreeRoots(roots);
//   setCounts(counts); // ✅ store counts in state

//   if (!ProjectManager.getSelectedCell() && roots.length > 0) {
//     const defaultCell = roots[0].name;
//     ProjectManager.setSelectedCell(defaultCell);
//     setActiveCell(defaultCell);
//     window.canvasEngine.draw();
//   }
// }, []);
   useEffect(() => {
  const rebuildTree = () => {
    const { roots, counts } = buildHierarchyTree();
    setTreeRoots(roots);
    setCounts(counts);

    // Optional: Set default selected cell if none is selected
    if (!ProjectManager.getSelectedCell() && roots.length > 0) {
      const defaultCell = roots[0].name;
      ProjectManager.setSelectedCell(defaultCell);
      setActiveCell(defaultCell);
      window.canvasEngine.draw();
    }
  };

  // Initial tree load
  rebuildTree();

  // Listen for global hierarchy updates
  window.addEventListener("hierarchy-updated", rebuildTree);

  // Cleanup on unmount
  return () => {
    window.removeEventListener("hierarchy-updated", rebuildTree);
  };
}, []);



  const handleSelect = (cellName) => {
    setActiveCell(cellName);
    ProjectManager.setSelectedCell(cellName);
   
    window.canvasEngine.draw();
  };

  return (
    <div style={{ padding: '10px', color: '#fff', maxHeight: '100%', overflowY: 'auto' }}>
      <h3>📁 Hierarchy</h3>
      {treeRoots.map(root => (
        <TreeNode key={root.name} node={root} activeCell={activeCell} onSelect={handleSelect} counts={counts}/>
      ))}
    </div>
  );
};

export default HierarchyPanel;


